import parseFromMsWord from './parseFromMsWord';
import parseFromWeb from './parseFromWeb';
import { MS_WORD, HTMLToJsonMLRule } from '@ali/4ever-cangjie';

export default function htmlToJsoML(pasteFrom): HTMLToJsonMLRule {
  if (pasteFrom === MS_WORD) {
    return parseFromMsWord();
  }
  return parseFromWeb();
};
